.header-galeria {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.7)),
    url(/build/img/fondo_galeria.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  height: 40rem;

  display: flex;
  justify-content: center;
  align-items: center;
  h1 {
    color: $blanco;
    font-weight: $bold;
    font-size: 5rem;
  }
}

/** FOTOGRAFIAS DE LA GALERIA **/
.galeria {

  margin-top: 5rem;
  margin-bottom: 5rem;
  @include tablet {
    @include grid(2, 3rem);
  }
  @include tabletL {
    @include grid(3, 3rem);
  }
  h2 {
    width: 100%;
    font-weight: 300;
  }

  img {
    -webkit-box-shadow: 10px 10px 122px -7px rgba(0, 0, 0, 0.75);
    border-radius: 5rem;
    width: 100%;
    height: 100%;
    padding: 1rem;
    transition: scale 1s;
    &:hover {
      scale: 1.05;
    }
  }

  article {
    display: grid;
    grid-template-rows: 20% 80%;
  }

}
