.servicios-in,
.servicios {
  opacity: 0;
  transform: scale(0.9);
  transition: .7s ease-in-out all;
  &.visible{
    opacity: 1;
    transform: scale(1);
  }
  margin-top: 10rem;
  @include tabletL {
    @include grid(3, 4rem);
  }
  a {
    transition: scale 1.2s;
    &:hover {
      scale: 105%;
    }
  }

  .descripcion-servicios-in {
    h2,
    h4 {
      text-align: left;
    }
    p {
      text-align: justify;
    }
    h2 {
      font-weight: bolder;
      span {
        color: $azul;
      }
    }
    h4 {
      font-size: 2rem;
      text-transform: uppercase;
      color: $grisClaro;
      margin: 0;
    }

    a:hover {
      color: $azul;
      border: 1px solid $azul;
    }
  }
}

.servicio-vip {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("/build/img/lavado_coches_incio.webp");
  transition: scale 1.2s;
  &:hover {
    scale: 105%;
  }
}
.servicio-integral {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("/build/img/lavado_integral_inicio.webp");
}

.servicio-integral,
.servicio-vip {
  margin: 2rem 0;
  @include tabletL {
    margin: 0;
  }
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-radius: 5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  h2 {
    color: $blanco;
    font-weight: bold;
    margin-top: 15rem;
  }
  p {
    font-size: 2rem;
    padding: 3rem;
    color: $azul;
  }
}

/**=====================SERVUCUIS ESPECIALES=================================================**/

.servicio-especiales {
  margin-top: 10rem;
  margin-bottom: 10rem;
  @include tabletL {
    @include grid(2, 4rem);
    h4 {
      grid-column: 1/3;
      grid-row: 1/2;
    }
    .especiales-tapizados {
      grid-column: 1/3;
      grid-row: 2/3;
    }
  }
  h4 {
    color: $grisClaro;
    font-size: 2rem;
    text-transform: uppercase;
  }
  a {
    transition: scale 1.2s;
    &:hover {
      scale: 105%;
    }
  }
  .especiales-tapizados {
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url("/build/img/tapizados_inicio2.webp");
  }
  .especiales-puliment {
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url("/build/img/pulimento_coches_inicio.webp");
  }
  .especiales-neumaticos {
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url("/build/img/neumaticos_inicio.webp");
  }
}
.especiales-neumaticos,
.especiales-puliment,
.especiales-tapizados {

  @include tabletL {
    margin: 0;
  }
  margin-bottom: 2rem;

  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-radius: 5rem;
  text-align: center;
  height: 50rem;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  p {
    font-size: 2rem;
    padding: 3rem;
    color: $azul;
  }

  h2 {
    color: $blanco;
    font-size: 4rem;
    text-transform: uppercase;
    font-weight: bold;
  }
  opacity: 0;
  transform: scale(0.9);
  transition: .7s ease-in-out all;
  &.visible{
    opacity: 1;
    transform: scale(1);
  }
}

/**==============================OFERTAS==================================**/

.ofertas {
  .servicios {
    @include tabletL {
      grid-template-columns: 50% 50%;
    }
  }
  margin-top: 15rem;
  h2,
  h4,
  p {
    text-align: center;
  }
  h2 {
    font-weight: bolder;
    span {
      color: $azul;
    }
  }
  h4 {
    font-size: 5rem;
    text-transform: uppercase;
    color: $negro;
    margin: 0;
  }

  .con-oferta {
    h3 {
      span {
        opacity: 0.7;
        font-size: 3rem;
        text-decoration: line-through;
        text-decoration-color: red;
      }
    }
  }
}
