.navegacion {
  margin: 0;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear,
    height 0.5s linear;

  @include tabletL {
    display: flex;
    align-items: center; //centrado verticalmente
    margin-top: 0rem;
    opacity: 1;
    visibility: visible;
  }
  a {
    margin: 0.8rem 0rem;
    padding: 1.5rem 0rem;
    color: $blanco;
    text-decoration: none;
    font-size: 2.2rem;
    display: block;
    text-align: center;
    text-transform: uppercase;
    

    @include tabletL {
      margin: 0rem;
      margin-right: 4rem;
      font-size: 1.8rem;

      &:last-of-type {
        margin-right: 0; //para eliminar el margin del ultimo elemento de la NAVEGACION
      }
    }
    &:hover {
      color: $azul;
    }
  }
}

/**================================NAVEGACION SERVICIOS SCROOL============================**/
.navegacion_servicios {
  margin-top: 3rem;
  margin-bottom: 3rem;
  display: grid;
  @include tabletL {
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
  a {
    text-align: center;
    text-decoration: none;
    color: $negro;
    font-size: 2rem;
    &:hover {
      color: $azul;
    }
  }
}

/**==================================MENU HAMBURGUESA======================================**/

.mobile-menu {
  @include tabletL {
    display: none;
  }
}
.mostrar {
  margin-top: 2rem;
  @include tabletL{
    margin-top: 0rem;
  }
  visibility: visible;
  opacity: 1;
  height: auto;
}
