/** UI **/
.header-inicio {
  margin: 0;
  background-size: cover;
}

.header {
  background-color: #333333;
  opacity: 0.9;
  margin: 0;
  padding: 1rem;
  z-index: 1;
  transition: background-color 1s width 1s;
}
.header.fijo {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.856);
  backdrop-filter: blur(90px);
}

.fondo {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)), url(../../../build/img/fondo_global1.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  min-height: 30rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.fondo h1 {
  font-size: 5rem;
  margin: 0;
  padding: 5rem;
  color: #FFFFFF;
  font-weight: bold;
}

.bodyinicio {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.1)), url(../../../build/img/fondo_inicio1.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
}

.contenido-header {
  min-height: 50rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
}

.barra {
  color: transparent;
  display: flex;
  flex-direction: column;
  padding: 1rem 1rem;
}
.barra .barra-horizontal {
  display: flex;
  justify-content: space-between;
}
.barra .barra-horizontal .logo,
.barra .barra-horizontal .mobile-menu {
  margin: 0;
  padding: 0;
  margin-left: 1rem;
}
.barra .barra-horizontal .logo img,
.barra .barra-horizontal .mobile-menu img {
  justify-content: space-between;
  width: 100%;
  max-height: 5rem;
}
@media (min-width: 992px) {
  .barra {
    align-items: center;
  }
}
@media (min-width: 992px) {
  .barra a {
    max-width: 20rem;
  }
}
@media (min-width: 992px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .mobile-menu:hover {
  cursor: pointer;
}

.herder-texto h2 {
  margin-top: 5rem;
  color: #FFFFFF;
  font-weight: 700;
  text-align: left;
  font-size: 2.5rem;
}
@media (min-width: 992px) {
  .herder-texto h2 {
    font-size: 3.4rem;
  }
}
@media (min-width: 992px) and (min-width: 992px) {
  .herder-texto h2 {
    margin-top: 2rem;
  }
}
.herder-texto a {
  text-transform: uppercase;
}
.herder-texto a:hover {
  border: 1px solid #07e9e5;
  color: #07e9e5;
}

.navegacion {
  margin: 0;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 992px) {
  .navegacion {
    display: flex;
    align-items: center;
    margin-top: 0rem;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  margin: 0.8rem 0rem;
  padding: 1.5rem 0rem;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 992px) {
  .navegacion a {
    margin: 0rem;
    margin-right: 4rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #07e9e5;
}

/**================================NAVEGACION SERVICIOS SCROOL============================**/
.navegacion_servicios {
  margin-top: 3rem;
  margin-bottom: 3rem;
  display: grid;
}
@media (min-width: 992px) {
  .navegacion_servicios {
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}
.navegacion_servicios a {
  text-align: center;
  text-decoration: none;
  color: #000000;
  font-size: 2rem;
}
.navegacion_servicios a:hover {
  color: #07e9e5;
}

/**==================================MENU HAMBURGUESA======================================**/
@media (min-width: 992px) {
  .mobile-menu {
    display: none;
  }
}

.mostrar {
  margin-top: 2rem;
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 992px) {
  .mostrar {
    margin-top: 0rem;
  }
}

.footer {
  padding: 3rem 0;
  background-color: #333333;
  text-align: center;
  margin: 0;
}
@media (min-width: 992px) {
  .footer {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
    justify-content: space-between;
  }
}
.footer p, .footer h2 {
  color: #FFFFFF;
  margin: 0;
}
.footer p:last-of-type, .footer h2:last-of-type {
  margin: 0;
}
.footer p, .footer a {
  font-size: 1.2rem;
  text-align: center;
}
.footer h2 {
  margin: 2rem 0;
}
.footer nav {
  display: block;
  margin: 0;
}
.footer nav a {
  margin: 0;
}
.footer .derechos {
  grid-column: 1/5;
  margin-bottom: 1rem;
}

.apartados {
  padding-top: 3rem;
  padding-bottom: 3rem;
  text-align: center;
}
.apartados div,
.apartados a {
  margin-bottom: 2rem;
}
@media (min-width: 992px) {
  .apartados {
    gap: 2rem;
    display: grid;
    grid-template-columns: 2fr 3fr 2fr;
  }
}
.apartados a {
  border: 3px solid transparent;
  text-decoration: none;
  transition: color 0.8s, border 0.8s, background-color 0.8s;
}
.apartados a:hover {
  border: 3px solid #07e9e5;
  color: #07e9e5;
  background-color: #333333;
}
.apartados a:hover p {
  color: #07e9e5;
}
.apartados .apartado-nav {
  height: 30rem;
}
@media (min-width: 992px) {
  .apartados .apartado-nav {
    height: 100%;
  }
}
.apartados .apartado-nav .mapa {
  width: 100%;
  height: 100%;
  border-radius: 5rem;
}
.apartados .apartado-contacto,
.apartados .apartado-horario {
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 5rem;
  opacity: 0.6;
}
@media (min-width: 992px) {
  .apartados .apartado-contacto,
.apartados .apartado-horario {
    margin: 0;
  }
}
.apartados .apartado-contacto h2,
.apartados .apartado-horario h2 {
  text-transform: uppercase;
  font-weight: normal;
}
.apartados .titulo {
  color: #FFFFFF;
  font-weight: bold;
  font-weight: normal;
}

.servicios-in,
.servicios {
  opacity: 0;
  transform: scale(0.9);
  transition: 0.7s ease-in-out all;
  margin-top: 10rem;
}
.servicios-in.visible,
.servicios.visible {
  opacity: 1;
  transform: scale(1);
}
@media (min-width: 992px) {
  .servicios-in,
.servicios {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.servicios-in a,
.servicios a {
  transition: scale 1.2s;
}
.servicios-in a:hover,
.servicios a:hover {
  scale: 105%;
}
.servicios-in .descripcion-servicios-in h2,
.servicios-in .descripcion-servicios-in h4,
.servicios .descripcion-servicios-in h2,
.servicios .descripcion-servicios-in h4 {
  text-align: left;
}
.servicios-in .descripcion-servicios-in p,
.servicios .descripcion-servicios-in p {
  text-align: justify;
}
.servicios-in .descripcion-servicios-in h2,
.servicios .descripcion-servicios-in h2 {
  font-weight: bolder;
}
.servicios-in .descripcion-servicios-in h2 span,
.servicios .descripcion-servicios-in h2 span {
  color: #07e9e5;
}
.servicios-in .descripcion-servicios-in h4,
.servicios .descripcion-servicios-in h4 {
  font-size: 2rem;
  text-transform: uppercase;
  color: #949494;
  margin: 0;
}
.servicios-in .descripcion-servicios-in a:hover,
.servicios .descripcion-servicios-in a:hover {
  color: #07e9e5;
  border: 1px solid #07e9e5;
}

.servicio-vip {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/build/img/lavado_coches_incio.webp");
  transition: scale 1.2s;
}
.servicio-vip:hover {
  scale: 105%;
}

.servicio-integral {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/build/img/lavado_integral_inicio.webp");
}

.servicio-integral,
.servicio-vip {
  margin: 2rem 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-radius: 5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 992px) {
  .servicio-integral,
.servicio-vip {
    margin: 0;
  }
}
.servicio-integral h2,
.servicio-vip h2 {
  color: #FFFFFF;
  font-weight: bold;
  margin-top: 15rem;
}
.servicio-integral p,
.servicio-vip p {
  font-size: 2rem;
  padding: 3rem;
  color: #07e9e5;
}

/**=====================SERVUCUIS ESPECIALES=================================================**/
.servicio-especiales {
  margin-top: 10rem;
  margin-bottom: 10rem;
}
@media (min-width: 992px) {
  .servicio-especiales {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .servicio-especiales h4 {
    grid-column: 1/3;
    grid-row: 1/2;
  }
  .servicio-especiales .especiales-tapizados {
    grid-column: 1/3;
    grid-row: 2/3;
  }
}
.servicio-especiales h4 {
  color: #949494;
  font-size: 2rem;
  text-transform: uppercase;
}
.servicio-especiales a {
  transition: scale 1.2s;
}
.servicio-especiales a:hover {
  scale: 105%;
}
.servicio-especiales .especiales-tapizados {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/build/img/tapizados_inicio2.webp");
}
.servicio-especiales .especiales-puliment {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/build/img/pulimento_coches_inicio.webp");
}
.servicio-especiales .especiales-neumaticos {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/build/img/neumaticos_inicio.webp");
}

.especiales-neumaticos,
.especiales-puliment,
.especiales-tapizados {
  margin-bottom: 2rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-radius: 5rem;
  text-align: center;
  height: 50rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  opacity: 0;
  transform: scale(0.9);
  transition: 0.7s ease-in-out all;
}
@media (min-width: 992px) {
  .especiales-neumaticos,
.especiales-puliment,
.especiales-tapizados {
    margin: 0;
  }
}
.especiales-neumaticos p,
.especiales-puliment p,
.especiales-tapizados p {
  font-size: 2rem;
  padding: 3rem;
  color: #07e9e5;
}
.especiales-neumaticos h2,
.especiales-puliment h2,
.especiales-tapizados h2 {
  color: #FFFFFF;
  font-size: 4rem;
  text-transform: uppercase;
  font-weight: bold;
}
.especiales-neumaticos.visible,
.especiales-puliment.visible,
.especiales-tapizados.visible {
  opacity: 1;
  transform: scale(1);
}

/**==============================OFERTAS==================================**/
.ofertas {
  margin-top: 15rem;
}
@media (min-width: 992px) {
  .ofertas .servicios {
    grid-template-columns: 50% 50%;
  }
}
.ofertas h2,
.ofertas h4,
.ofertas p {
  text-align: center;
}
.ofertas h2 {
  font-weight: bolder;
